<template>
  <div>
    <el-card>
      <div slot="header" class="clearfix">
        <span>工站编号 : {{ CurrentUserInfo.work_id }}</span>
        <span class="ml20">工站名称 : {{ workStationName }}</span>
        <span class="ml20">工站类型 : {{ CurrentUserInfo.work_type }}</span>
      </div>

      <el-form
        class=""
        style="padding: 0 60px 0 20px; width: 650px"
        ref="formRef"
        :model="form"
        :rules="formRules"
        label-width="100px"
      >
        <el-form-item prop="finished_product_code" label="产品编号">
          <el-input
            ref="firstInput"
            v-model="form.finished_product_code"
            @input="inputChange()"
          ></el-input>
        </el-form-item>
        <div
          v-for="(item, index) in form.response_datas"
          :key="index + ' onlyItem'"
        >
          <el-form-item
            :prop="'response_datas.' + index + '.matter_id'"
            :rules="[
              {
                required: true,
                message: '物料编码不能为空',
                trigger: 'blur',
              },
              {
                pattern: item.rule,
                message: '编码需要包含' + item.rule,
                trigger: 'blur',
              },
            ]"
            :label="item.matter_name"
          >
            <el-input
              :ref="'matterInput' + index"
              v-model="item.matter_id"
            ></el-input>
          </el-form-item>
        </div>

        <el-form-item
          prop="return_work_id"
          label="返回工站"
          v-if="form.finished_product_code"
        >
          <el-select
            v-model="form.return_work_id"
            placeholder="请选择"
            style="width: 100%"
          >
            <el-option
              v-for="(item, index) in WorkCodeNameList"
              :key="index + ' dialogForm'"
              :label="item.work_name"
              :value="item.work_id"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>

      　
      <div v-if="form.finished_product_code">
        <el-button @click="resetForm">重 置</el-button>
        <el-button type="primary" @click="dialogSave">确 定</el-button>
      </div>
    </el-card>
  </div>
</template>

<script>
import { baseManagement } from "@/request/api";
import { mapState, mapActions } from "vuex";
export default {
  name: "MissStationM",

  data() {
    return {
      timeout: "",
      // 筛选
      listQuery: {
        finished_product_code: "",
      },
      listQueryBase: {
        finished_product_code: "",
      },
      // 产品过站弹出层
      form: {
        return_work_id: "",
        finished_product_code: "",
        response_datas: [],
      },
      formBase: {
        return_work_id: "",
        finished_product_code: "",
        response_datas: [],
      },
      formRules: {
        finished_product_code: [
          { required: true, message: "请输入产品编号", trigger: "blur" },
        ],
        test_result: [
          { required: true, message: "请选择检验结果", trigger: "change" },
        ],
      },
      response_datasRules: {
        matter_id: [
          {
            required: true,
            message: "数量不能为空",
            trigger: "blur",
          },
        ],
      },
    };
  },

  computed: {
    ...mapState([
      "BomMatterCodeNameList",
      "WorkCodeNameList",
      "CurrentUserInfo",
    ]),
  },
  methods: {
    ...mapActions(["getLogisticBomMatterCodeName", "getLogisticWorkCodeName"]),
    inputChange(e) {
      if (this.timeout) {
        clearTimeout(this.timeout);
      }
      this.timeout = setTimeout(() => {
        this.getLogisticFinishedCodeGetMatterCodeID(this.form);
      }, 500);
    },
    // 通过产品编号查询物料 查询
    getLogisticFinishedCodeGetMatterCodeID(params) {
      baseManagement
        .getLogisticFinishedCodeGetMatterCodeID(params)
        .then((res) => {
          console.log("通过产品编号查询物料 查询", res);
          if (res.code == 0) {
            let resData = res.data;
            resData.forEach((resDataItem, index) => {
              this.BomMatterCodeNameList.forEach((BomMatterCodeNameItem) => {
                if (
                  resDataItem.matter_code == BomMatterCodeNameItem.matter_code
                ) {
                  resDataItem.matter_name = BomMatterCodeNameItem.matter_name;
                  resDataItem.rule = BomMatterCodeNameItem.rule;
                }
              });
            });
            this.form.response_datas = res.data;
          } else {
            this.$message({
              message: res.message,
              type: "error",
            });
          }
        });
    },
    // 重 置
    getDeleteFinishedCodeGetMatterCodeID(params) {
      baseManagement
        .getDeleteFinishedCodeGetMatterCodeID(params)
        .then((res) => {
          console.log("重 置 删除", res);
          if (res.code == 0) {
            this.form = JSON.parse(JSON.stringify(this.formBase));

            this.$message({
              message: res.message,
              type: "success",
            });
          } else {
            this.$message({
              message: res.message,
              type: "error",
            });
          }
        });
    },
    resetForm() {
      this.$confirm("确定执行重置操作吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        this.getDeleteFinishedCodeGetMatterCodeID(this.form);

        this.$nextTick(() => {
          this.$refs.formRef.clearValidate();
        });
      });
    },
    // 确定

    getLogisticPutFinishedCodeGetMatterCodeID(params) {
      baseManagement
        .getLogisticPutFinishedCodeGetMatterCodeID(params)
        .then((res) => {
          console.log("产品过站 修改", res);
          if (res.code == 0) {
            this.form = JSON.parse(JSON.stringify(this.formBase));
            this.$nextTick(() => {
              this.$refs.formRef.clearValidate();
            });
            this.$message({
              message: res.message,
              type: "success",
            });
          } else {
            this.$message({
              message: res.message,
              type: "error",
            });
          }
        });
    },
    dialogSave() {
      console.log("this.form", this.form);
      let params = JSON.parse(JSON.stringify(this.form));
      params.response_datas = JSON.stringify(this.form.response_datas);

      this.getLogisticPutFinishedCodeGetMatterCodeID(params);
    },
  },
  mounted() {
    // 产品　物料code和名称接口 查询
    this.getLogisticBomMatterCodeName();
    // 工站内码和工站名称接口： 查询
    this.getLogisticWorkCodeName();
    this.WorkCodeNameList.forEach((CodeNameListItem) => {
      if (this.CurrentUserInfo.work_id == CodeNameListItem.work_id) {
        this.workStationName = CodeNameListItem.work_name;
      }
    });

    this.$nextTick(() => {
      this.$refs.firstInput.focus();
    });
  },
};
</script>

